export function toggleComplete(itemId) {
    return {
        type: 'TOGGLE_COMPLETE',
        itemId
    }
}

export function changeFilter(filter) {
    return {
        type: 'CHANGE_FILTER',
        filter
    }
}

export function editItem(itemId) {
    return {
        type: 'EDIT'
    }
}


